import React from "react"
import styles from "./styles.module.scss"
import Layout from "~src/components/Layout"
import SEO from "~src/components/seo"

const PriceUpdateScript = () => {
  return (
    <Layout>
      <SEO title="Python Price Update Script" />
      <div className="container">{renderText()}</div>
    </Layout>
  )
}

const renderText = () => {
  return (
    <>
      <h2>Price Update Python Script</h2>
      <i>Technology: Python, xlrd</i>
      <a href="https://github.com/YozuBear/Price-Update-Script" target="_blank">
        [code]
      </a>
      <p>
        I wrote this python script for my father who works a sales. Every week,
        he updates the items in his excel spreadsheet with the new price lists
        compiled by his colleagues (also in excel format). Each of his colleague
        has their own style and format for the spreadsheet, and some items have
        the same IDs. Without unique IDs, the matching algorithm is doomed to
        become problematic. Therefore, my father invented his own naming system,
        adding a little <i>i</i> or <i>o</i> for the duplicated ones. This makes
        the matching ambiguous, so I implemented a weak matching and strong
        matching flag for this script. With strong matching, the price is only
        updated if they match exactly. On the other hand, weak matching first
        uses strong matching, then tries to match common prefix if strong
        matching does not find the item.
      </p>
    </>
  )
}

export default PriceUpdateScript
